var test = require('ava');
var request = require('supertest');
var app = require('../src/app');

test('Test root site', function (done) {
    request(app)
        .get('/').then(function (response) {

        //console.log(response.body);

        expect(response.statusCode).toBe(200);
        done();
    });
});


